<!--  -->
<template>
  <div class="box">
    <button @click="add($event)">+</button>
    <div class="gou">
    </div>
  </div>
</template>

<script>
import Ball from "../../config/Ball"
import "../../config/ball.css"
export default {
  data () {
    return {
    };
  },

  components: {},

  computed: {},

  mounted(){},

  created(){},

  methods: {
    add(e){
      Ball(e)
    }
  }
}

</script>
<style scoped>
.box{
  position: relative;
  
}
.gou{

  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  background: rebeccapurple;
  top: 10rem
}
button{
  margin-left: 5rem
}
</style>


