export default function Ball(event){
    // 创建一个div
    console.log(event)
    let div=document.createElement("div")
    div.className='ballFather'
    document.body.appendChild(div)
    // 大div里面创建一个小的div
    let sonDiv=document.createElement('div')
    sonDiv.classList="ballSon";
    div.appendChild(sonDiv)

    // 获取按钮位置
   let top=event.clientY;
   let left=event.clientX;
    // 创建的div初始位置    
    div.style.left=left+'px'
    div.style.top=top+'px'
    // 获取小球到底部距离
    // 获取小小求到左边的距离
    let y=(window.innerHeight-top)
    div.style.transform=`translateY(${y+50}px)`
     div.style.webkitTransform=`translateY(${y+50}px)`; /* Safari and Chrome */


     sonDiv.style.transform=`translateX(${-left+50}px)`
     sonDiv.style.webkitTransform=`translateX(${-left+50}px)`; /* Safari and Chrome */
        setTimeout(()=>{
        //     document.querySelector('.sc').classList 找到class sc 的元素 添加一个动画 
         document.querySelector('.che').classList.add("animation");
       setTimeout(()=>{
              document.querySelector('.che').classList.remove("animation");
            },400)
       
         },500)





}


